.navLink {
    display: block;
    border-bottom: 8px solid $white;
    padding: 24px 0 16px 0;
    @include pad {
        padding: 16px 0 8px 0;
    };
}
.navLink:hover {
    border-bottom: 8px solid $primary;
}
.bg-productHeader {
    height: 296px;
    @include mobile {
        height: 136px;
    };
}
.product-headerOa {
    width: 100%;
    height: 100%;
    background: url("../images/product-header-1.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @include mobile {
        background: url("../images/product-header-sm-1.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    };
}
.product-headerOb {
    width: 100%;
    height: 100%;
    background: url("../images/product-header-2.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @include mobile {
        background: url("../images/product-header-sm-2.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    };
}
.color-brown::before {
    content: "";
    display: inline-block;
    width: 24px;
    height: 24px;
    background-color: #5F3E2D;
    border-radius: 4px;
    margin-right: 8px;
}
.color-lightBrown::before {
    content: "";
    display: inline-block;
    width: 24px;
    height: 24px;
    background-color: #B75929;
    border-radius: 4px;
}
.pagination {
    display: flex;
    justify-content: center;
}
.page-item {
    border: 1px solid #555555;
    margin-left: -1px;
}
.page-link {
    display: block;
    color: $black;
    padding: 12px 16px;
    @include mobile {
        padding: 12px 15px;
    };
}
.page-link:hover {
    background-color: #F2F2F2;
}
.extraSpacing {
    padding: 11px 12px;
}